.HomeWrapper {
    width: 100%;
    height: 95%;

    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
    align-items: center;

    margin: 0;
    padding: 0;
    position: relative;
}

.HomeInnerTitle {
    color: #000000;
    font-family: Roboto Condensed, serif;
    font-size: calc(20px + (36 - 20) * ((100vw - 280px) / (1920 - 280)));
    margin: 2% 0 0 0;
}

.HomeInnerTitleName {
    color: #000000;
    font-family: Roboto Condensed, serif;
    font-size: calc(36px + (48 - 36) * ((100vw - 280px) / (1920 - 280)));
}

.HomeCatalogue {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.HomeButtonMeme,
.HomeButtonPsy,
.HomeButtonLg {
    border: 5px solid #000000;
    box-shadow: -15px 15px 0 #000000;
    border-radius: 0;

    height: calc(156px + (375 - 156) * ((100vw - 280px) / (1920 - 280)));
    width: calc(156px + (480 - 156) * ((100vw - 280px) / (1920 - 280)));

    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;

    margin: 1.25%;
    padding: 0;
}
.HomeButtonMeme:hover,
.HomeButtonLg:hover,
.HomeButtonPsy:hover {
    background-image: url('../img/dots.png');
    background-blend-mode: soft-light;
    background-size: contain;
    background-attachment: fixed;
}
.HomeButtonMeme {
    background-color: #D7A6D9;
}
.HomeButtonPsy {
    background-color: #6ACD94;
}
.HomeButtonLg {
    background-color: #41A6E1;
}

.HomeButtonText {
    font-family: Roboto Condensed, serif;
    font-size: calc(36px + (48 - 36) * ((100vw - 280px) / (1920 - 280)));
    font-weight: bold;
    color: #000000;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
}