
.nav {
    background-color: #FEE9C6;
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 2px solid #000000;
}


.logoBox {
    background-color: #FEE9C6;

    height: fit-content;
    width: fit-content;
    /* Display */
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    place-content: center;
    flex-wrap: nowrap;
}

.logoText {
    background-color: #FEE9C6;
    color: #000000;
    font-family: "Parisienne", serif;
    font-size: 48px;

    /* Size */
    height: fit-content;
    width: fit-content;

    /* Display */
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    place-content: center;
    flex-wrap: nowrap;

    /* Others */
    margin: 0 0 0 15px;
    padding: 0;
}

.login_button {
    width: 10%;
    color: black;
    border: black solid 2px;
    margin: 0 0 0 2%;
    font-family: Roboto, serif;
    font-weight: 400;
    text-align: center;
    padding: 0.3%;
    font-size: larger;
    background-color: #FEE9C6;
}

.reg_button {
    width: 10%;
    color: #FFFFFF;
    border: black solid 2px;
    margin: 0 0 0 2%;
    font-family: Roboto, serif;
    font-weight: 400;
    text-align: center;
    padding: 0.3%;
    font-size: larger;
    background-color: #000000;
}

.inner {
    width: 100%;
    height: 100%;
}

.outer {
    width: 25%;
    height: 50em;
    display: flex;
    margin-top: 10%;
}

.form {
    border: 0.05em solid #000000;
    width: 100%;
    background-color: #FEEAC5;
}

.Log_in {
    background-color: #FFD203;
    text-align: center;
    padding: 2%;
    margin: 0;
    font-family: Roboto Condensed, serif;
    font-weight: 1000;
    font-size: x-large;
}

.input {
    width: 90%;
    margin: 5%;
    background-color: #FFFFFF;
    border: 1px solid #000000;
    border-radius: 60px;
}

.enter {
    width: 46%;
    background-color: #000000;
    font-family: Roboto, serif;
    text-align: center;
    border-radius: 60px;
    margin: 4% 4% 4% 27%;
    height: 3em;
    font-size: large;
    padding: 0;
}

.Sign_up {
    background-color: #EF4E32;
    text-align: center;
    padding: 2%;
    margin: 0;
    font-family: Roboto Condensed, serif;
    font-weight: 1000;
    font-size: x-large;
}